import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

import { useSelector, useDispatch } from 'react-redux';
import { fetchSignUp } from '../../store/authSlice';

import { Formik, Form } from 'formik';
import FormControl from '../forms/FormControl';
import { signUpSchema } from '../forms/schema';

import ModalWindow from '../ModalWindow';


function RegistrationForm() {
	let navigate = useNavigate();

	let dispatch = useDispatch();
	let {fetchStatus, fetchError} = useSelector(state => state.auth);
	
	let [modalWindowIsOpen, setModalWindowIsOpen] = useState(false);

	let initialValues = {
		email: '',
		password: '',
		firstName: '',
		lastName: ''
	};

	let onSubmit = (values, {resetForm, setStatus}) => {
		try {
			dispatch(fetchSignUp(values));
			resetForm();
			setModalWindowIsOpen(!modalWindowIsOpen)
		}
		catch(err) {
			setStatus({
        msg: `  : ${err.message}!`
      })
		}
	};
	
	let toggleModal= () => {
    setModalWindowIsOpen(!modalWindowIsOpen);
		navigate('/');
  };

	return (
		<>
			<div className='form__intro'>
				<p className='form__intro-text'>
					   
					   .
					<br/>
					<br/>
					,     
					  &#171;&#187;.
				</p>
			</div>
			<div className='form-mobile'></div>
			<Formik
				initialValues = {initialValues}
				validationSchema = {signUpSchema}
				onSubmit = {onSubmit}
			>
				{({isSubmitting, handleReset, status, dirty, errors, touched}) => (
					<Form className='form'>
						{status && status.msg && <p style={{color: "#fff"}}>{status.msg}</p>}
						<FormControl 
							control='input' 
							type='email' 
							name='email' 
							label='Email' 
							errors={errors}
							touched={touched}
						/>
						<FormControl 
							control='input' 
							type='password' 
							name='password' 
							label=''
							errors={errors}
							touched={touched}
						/>
						<FormControl 
							control='input' 
							name='firstName' 
							label=''
							errors={errors}
							touched={touched}
						/>
						<FormControl 
							control='input' 
							name='lastName' 
							label=''
							errors={errors} 
							touched={touched}
						/>
						<div className='form__buttons'>
							<button 
								type='button' 
								disabled={isSubmitting || !dirty} 
								onClick={handleReset}
								className='form__btn'
							></button>
							<button
								type='submit' 
								disabled={isSubmitting || !dirty}
								className='form__btn'
							></button>
						</div>
					</Form>
					)
				}
			</Formik>
			<ModalWindow
				isOpen={modalWindowIsOpen}
				onRequestClose={toggleModal}
				title = {fetchStatus === 'loading' ? ' ...' : fetchStatus === 'resolved' ? '  ' : ''}
				textErr =  {fetchError && `: ${fetchError} !`}
				onClick = {toggleModal}
			/>		
		</>
	)
}

export default RegistrationForm;